<template>
  <h4>{{ title }}</h4>
  <div>
    <textarea rows="5" col="10" v-model="input" class="form-control"></textarea>
    <button class="btn btn-gradient-danger" @click="add">Agregar Palabras</button>
  </div>
  <div>
    <p v-for="(word, i) in words" :key="i">
      {{ word }}
      <button class="close" @click="() => this.remove(i)"></button>
    </p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      input: "",
      words: [],
    };
  },
  props: ["removeCallback", "addCallback", "title"],
  methods: {
    remove(i) {
      this.words.splice(i, 1);
      if (this.removeCallback) this.removeCallback(this.words);
    },
    add() {
      console.log(this.word);
      console.log(this.words);

      const wordList = this.input.split("\n");
      for (let i = 0; i < wordList.length; i++) {
        const word = wordList[i].trim();
        if (word) {
          this.words.push(word);
        }
      }
      this.input = "";
      this.addCallback(this.words);

    },
  },
};
</script>

